/*eslint-disable*/ 
import React, { useState } from 'react';
import './App.css';
import {Navbar,Container,Nav,NavDropdown,Button,Jumbotron,Card } from 'react-bootstrap';
import './App1.scss';
import Data from './data.js';
import { Route,Link,Switch } from 'react-router-dom';
import About from './about';
import axios from 'axios';


function App() {

let [신발,신발변경] = useState(Data);
// console.log(신발)

let[로딩중, 로딩변경] =useState(false);

let[재고, 재고변경]= useState([9,10,11]);

function sortName(){

  let newArr = [...신발]
  newArr.sort((a,b)=>{
    return a.price - b.price;
  })
  // console.log(newArr);

  return 신발변경(newArr)
};


return (
<div className="App">
  <Navbar bg="light" expand="lg">
    <Container>
      <Navbar.Brand href="#home">React-Bootstrap</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="me-auto">
          <Nav.Link>
            <Link to='/'>Home</Link>
          </Nav.Link>
          <Nav.Link>
            <Link to='/about'>어바웃</Link>
          </Nav.Link>
          <NavDropdown title="Dropdown" id="basic-nav-dropdown">
            <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
          </NavDropdown>
        </Nav>
      </Navbar.Collapse>
    </Container>
  </Navbar>


  <Switch>
    <Route exact path='/'>
      <Jumbotron className='background'>
        <h1>Hello, world!</h1>
        <p>
          This is a simple hero unit, a simple jumbotron-style component for calling
          extra attention to featured content or information.
        </p>
        <p>
          <Button variant="primary">Learn more</Button>
        </p>
      </Jumbotron>

      <div className='container'>
        <h3>상품목록 <button className='btn btn-danger' onClick={sortName}>가격순</button></h3>
        <div className='row'>
          {
            신발.map((a,i)=>{
              return <Produce key={i} a = {a}/> 
            })
          }

          {
            로딩중 === true
            ? <Loding name='로딩중입니다.'></Loding>
            : null

          }
          
          
          <button className='btn btn-danger m-auto ' onClick={()=>{
            로딩변경(true);
            
            axios.get('https://codingapple1.github.io/shop/data2.json').then((result)=>{
              신발변경([...신발,...result.data]);
              
              
              setTimeout(()=>{로딩변경(false)},1000)


            }).catch(()=>{
              console.log('아직임')
            })
          }}>더보기</button>

          

        </div>
    </div>


    </Route>

    <Route path='/about/:id'>
      <About 신발={신발} 재고={재고} 재고변경={재고변경} />
    </Route>

  </Switch>


</div>
);
}



function Produce(props){
  return (<div className='col-md-4 my-3'>
  <Card style={{ width: '100%' }}>
    <Card.Img variant="top" src={'https://codingapple1.github.io/shop/shoes'+(props.a.id +1)+'.jpg'} />
    <Card.Body>
      <Card.Title>{props.a.title}</Card.Title>
      <Card.Text>{props.a.content}</Card.Text>
      <Card.Text>{props.a.price}</Card.Text>
      <Button variant="primary" onClick={()=>{location.href='./about/'+ props.a.id}}>상세보기</Button>
    </Card.Body>
  </Card>
</div>
    
  );
};


function Loding(props){
  return(
    <div className='container mb-4'>
      <div className='my_alert'>{props.name}</div>
    </div>
  )
}




export default App;