import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom/cjs/react-router-dom.min";
import './App1.scss';




function About(props){

    let params = useParams().id;
    // console.log(params)

    let history =useHistory();
    let [alert, alert변경] = useState(true);

    useEffect(()=>{
        let time = setTimeout(()=>{alert변경('false')},2000)
        return ()=>{ clearTimeout(time)}
    },[]);


    function 재고업데이트(){
        let newArr = [...props.재고]
        
        if(newArr[params] <= 0){
            newArr[params] = '제고없음';
            
        }else{
            newArr[params] -= 1;
        }

        props.재고변경(newArr);
        // console.log(newArr);
    };
   


    return(
        <div className="container">
            {
                alert == true 
                ?<div className="my_alert"><p>재고가 얼마 남지 않았어요!!</p></div>
                : null
            }
            <div className="row">
                <div className="col-md-6">
                    <img src={`https://codingapple1.github.io/shop/shoes${(parseInt(params) +1)}.jpg`} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{props.신발[params].title}</h4>
                    <p>{props.신발[params].content}</p>
                    <p>{props.신발[params].price}원</p>
                    <p>재고{props.재고[params]}</p>
                    <button className="btn btn-danger" onClick={재고업데이트} >주문하기</button>
                    <button className="btn btn-danger" onClick={()=>{history.goBack()}}>뒤로가기</button>
                </div>
            </div>
        </div>
    )
};

export default About;